@import "variables";
@import "transitions";

@import url('https://fonts.googleapis.com/css?family=Libre+Baskerville:400,700|Raleway:300,400,700&subset=latin-ext');
html, .root {
	font-size: 16px;
	background-color: #eee;
	margin: 0;
	padding: 0; }

body {
	margin: 0;
	padding: 0;
	height: 100%;
	max-width: 100%;
	overflow-x: hidden;
	overflow-y: scroll; }

body #root .main-wrapper {
	background-color: #eee;
	background: {
		size: cover;
		repeat: no-repeat;
		position: center; } }

*, button {
	font-family: 'Raleway', Helvetica, Arial, Helvetica, sans-serif;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	color: $dark-grey; }

h1, h2, h3, h4, h5, h6 {
	width: 100%;
	text-align: center;
	font: {
		weight: 700; }
	color: $beige; }
h1 {
	font-size: 2.2rem; }
h2 {
	font-size: 1.8rem; }
h3 {
	font-size: 1.6rem; }
h4, h5 {
	font-size: 1.4rem; }
h6 {
	font-size: 1.2rem; }
button {
	border: none;
	outline: none;
	&:focus {
		outline: none; }
	&:hover {
		cursor: pointer; } }

.page-wrapper {
	width: 100vw;
	max-width: 100%; }


section.content {
	height: 100%; }

a {
	text-decoration: none;
	color: inherit;
	&:hover {
		text-decoration: none; } }

// Useful classes

.flex-center {
	display: flex;
	align-items: center;
	justify-content: center; }

.flex-column-center {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center; }





